import React from "react";
import "../node_modules/bootstrap/dist/css/bootstrap.min.css";
import "../node_modules/bootstrap/dist/js/bootstrap.bundle";
import Home from "./Home";
import About from "./About";
import Service from "./Service";
import Contact from "./Contact";
import Navbar from "./Navbar";
import {BrowserRouter} from "react-router-dom";
import Footer from "./Footer";
import Webdevelopment from './Courses/Webdevelopment';
import Frontend from "./Courses/Frontend";
import Backend from "./Courses/Webdev/Back/Backend";
import Php from "./Courses/Webdev/Back/Php/Php";
import Language from "./Courses/Langs/Language";
import Artificial from "./Courses/Ai/Artificial";
import { Switch, Route, Redirect,} from "react-router-dom";
import ScrollToTop from "react-scroll-to-top";



class App extends React.Component {
    render(){
  return (

    <div>
    <Navbar/>
    <ScrollToTop/>
    <Switch>
    <Route exact path="/" component={Home} />
    <Route exact path="/About" component={About} />
    <Route exact path="/Service" component={Service} />
    <Route exact path="/Contact" component={Contact} />
    <Route exact path="/Webdevelopment" component={Webdevelopment} />
    <Route exact path="/Frontend" component={Frontend} />
    <Route exact path="/Backend" component={Backend} />
    <Route exact path="/Php" component={Php} />
    <Route exact path="/Language" component={Language} />
    <Route exact path="/Artificial" component={Artificial} />
    <Redirect to = "/" />   

    
    </Switch>
     <Footer/>
    </div>
    );
};
}
export default App;