import React from "react"; 
import web from "../src/coffee.jpg";
import { NavLink, Route, Link} from "react-router-dom";





const Cards = (props) => {


  return (
<React.Fragment>
      
         
                  <div className='col-md-4 col-sm-4 mx-auto'>
                               <div className="card" >
                                <img className="card-img-top" src={props.imgsrc}  alt={props.imgsrc}/>
                                       <div className="card-body">


                                          <Link id="title" to={props.link}
                                          className="card-title font-weight-bold" >{props.title}</Link>

                                           <p className="card-text">{props.text}</p> 


                                         <a href={props.button} target = "_blank" rel= "noopener noreferrer"
                                          className="btn btn-primary"> Get Full Course </a>

                                       </div>
                                </div>

                  </div>
                 

</React.Fragment>



                  
    );
};

export default Cards;
